import {
  SIGN_IN,
  SIGN_OUT,
  ADD_TO_CART,
  FETCH_PRODUCTS,
  CLEAR_CART,
  DELETE_FROM_CART,
} from "./types";

export const signIn = (userId) => {
  return {
    type: SIGN_IN,
    payload: userId,
  };
};

export const signOut = () => {
  return {
    type: SIGN_OUT,
  };
};

export const addToCart = (product, userId, count) => async (dispatch) => {
  const response = await fetch(
    `https://shop-e49cd-default-rtdb.firebaseio.com/${userId}/${product.id}.json`,
    {
      method: "PUT",
      body: JSON.stringify({ ...product, count: count }),
    }
  );
  const res = await response.json();
  dispatch({ type: ADD_TO_CART, payload: res });
};

export const deleteFromCart = (product, userId) => async (dispatch) => {
  await fetch(
    `https://shop-e49cd-default-rtdb.firebaseio.com/${userId}/${product.id}.json`,
    { method: "DELETE" }
  );
  dispatch({ type: DELETE_FROM_CART, payload: product.id });
};

export const fetchProducts = (userId) => async (dispatch) => {
  const response = await fetch(
    `https://shop-e49cd-default-rtdb.firebaseio.com/${userId}.json`,
    { method: "GET" }
  );
  const res = await response.json();
  dispatch({ type: FETCH_PRODUCTS, payload: res });
};

export const clearCart = () => {
  return { type: CLEAR_CART };
};
