import React from "react";
import ReactDOM from "react-dom";
import { connect } from "react-redux";
import history from "../history";

const Alert = ({ isSignedIn }) => {
  const timeout = setTimeout(() => {
    history.goBack();
  }, 3000);

  const goBack = () => {
    clearTimeout(timeout);
    history.goBack();
  };

  const handleAlertContent = () => {
    return isSignedIn ? "已成功加入購物車" : "請先登入帳號";
  };

  return ReactDOM.createPortal(
    <div className="alert" onClick={goBack}>
      <div className="alert-content" onClick={(e) => e.stopPropagation()}>
        <div>{handleAlertContent()}</div>
        <div>在3秒後將自動關閉此視窗</div>
        <button onClick={goBack}>確定</button>
      </div>
    </div>,
    document.querySelector("#alert")
  );
};

const mapStateToProps = (state) => {
  return { isSignedIn: state.auth.isSignedIn };
};

export default connect(mapStateToProps)(Alert);
